//类型相关
var types = {
    //是否为空对象
    isEmptyPlainObject: function (object) {
        return JSON.stringify(object) === "{}";
    },
    //是否为string
    isStr: function (val) {
        return typeof val === "string";
    },
    //是否为obj对象
    isPlainObject: function (object) {
        return !!(object &&
            !this.isArray(object) &&
            (this.isFunction(object.toString)
                ? object.toString() === "[object Object]"
                : true) &&
            object.constructor === Object);
    },
    //是否为数组
    isArray: function (val) {
        return val instanceof Array;
    },
    //是否为对象Object Object
    isObject: function (object) {
        return this.isPlainObject(object) || this.isArray(object);
    },
    //是否有值
    isDef: function (val) {
        return val !== undefined && val !== null;
    },
    //是否为undefine 或者 null
    isUndef: function (val) {
        return val === undefined || val === null;
    },
    //字符串是否为空
    isEmptyStr: function (val) {
        return val.trim().length === 0;
    },
    //是否为true
    isTrue: function (bool) {
        return bool === true;
    },
    //是否为false
    isFalse: function (bool) {
        return bool === false;
    },
    //是否为function
    isFunction: function (fn) {
        return typeof fn === "function";
    },
    //是否为error
    isError: function (error) {
        return error instanceof Error;
    },
    //是否为布尔值
    isBoolean: function (bool) {
        return typeof bool === "boolean";
    },
};
export default types;
